/* CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    user-select: none;
    cursor: default;
}

body,
html {
    background: black;
    color: white;
    font-family: Arial, sans-serif;
    height: 100vh;
}

img {
    max-width: 100%;
    height: auto;
}

#root {
    height: 100%;
}

.layout {
    position: relative;
    height: calc(var(--vh, 1vh) * 100);
    background: repeating-linear-gradient(45deg,
            rgba(32, 32, 32, 0.5),
            rgba(32, 32, 32, 0.5) 10px,
            rgba(26, 26, 26, 0.5) 10px,
            rgba(26, 26, 26, 0.5) 20px), linear-gradient(0deg, rgba(157, 101, 30, 0.5) 5%, rgba(0, 0, 0, 1) 45%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }

    10% {
        opacity: 1;
        transform: translateY(0);
    }

    90% {
        opacity: 1;
        transform: translateY(0);
    }

    100% {
        opacity: 0;
        transform: translateY(-20px);
    }
}

.prompt-container {
    width: 100%;
    position: fixed;
    top: 1%;
    /* Adjust this value to lower the prompt */
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    /* Ensures the prompt doesn't block other interactions */
}

.prompt {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 4px;
    background: rgba(51, 51, 51, 0.5);
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    padding: 1.75rem 1rem;
    border-radius: 24px;
    animation: fadeInOut 4s ease-in-out;
}

.prompt p {
    font-size: 1rem;
    color: #fff;
}


/* */
.main {
    overflow: auto;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background: repeating-linear-gradient(45deg,
            rgba(32, 32, 32, 0.5),
            rgba(32, 32, 32, 0.5) 10px,
            rgba(26, 26, 26, 0.5) 10px,
            rgba(26, 26, 26, 0.5) 20px),
        radial-gradient(circle, rgba(241, 158, 54, 1) 0%, rgba(0, 0, 0, 1) 75%);
    background-position: center;
    padding-bottom: 128px;
}

.coin-container {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0 10%;
}

.plus-num {
    position: absolute;
    color: white;
    font-size: 32px;
    font-weight: bold;
    pointer-events: none;
    animation: fadeOutUp 1s forwards;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes fadeOutUp {
    0% {
        opacity: 1;
        transform: translateY(0);
    }

    100% {
        opacity: 0;
        transform: translateY(-200px);
    }
}

.counter-container {
    width: 100%;
    font-size: 2.75rem;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.counter-container img {
    transition: transform 0.1s ease;
}

.counter-container img.coin-enlarged {
    transform: scale(1.25);
}

.energy-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    gap: 4px;
}

.energy {
    display: flex;
    justify-content: center;
    width: 100%;
}

.energy-count {
    display: flex;
    flex-direction: column;
}

.energy-count span {
    color: #ffffff;
    font-weight: bold;
    font-size: 1.5rem;
}

.gray {
    color: #d7d7d798 !important;
    font-size: 0.75rem !important;
}

.progress-bar {
    width: 80%;
    height: 20px;
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    overflow: hidden;
    margin-top: 10px;
    padding: 4px;
    box-sizing: border-box;
}

.progress-bar-fill {
    height: 100%;
    background: linear-gradient(278deg, rgba(253, 237, 201, 1) 2%, rgba(241, 158, 54, 1) 52%, rgba(185, 103, 0, 1) 88%);
    width: 0;
    border-radius: 8px;
    transition: width 0.3s ease-in-out;
    box-shadow: 0 0 1px rgba(253, 237, 201, 0.5),
        0 0 10px rgba(241, 158, 54, 0.5),
        0 0 15px rgba(185, 103, 0, 0.5);
}


/* BOOST PAGE */
.boost-container {
    overflow: auto;
    height: 100%;
    width: 100%;
    padding-bottom: 128px;
}

.boost-counter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 0;
    gap: 16px;
}

.boost-counter p:first-child {
    color: #c1c1c1;
}

.boost {
    margin: 20px;
    background-color: rgba(51, 51, 51, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    border-radius: 32px;
    padding: 24px 0;
}

.boost-upgrade {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 32px;
}

.upgrade-section {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.upgrade-section-1 {
    display: flex;
    align-items: center;
    gap: 16px;
}

.upgrade-section-1 div:first-child {
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(218, 218, 218, 0.1);
    border-radius: 20px;
}

.upgrade-info {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.upgrade-info div {
    display: flex;
    align-items: center;
    gap: 4px;
}

.upgrade-title {
    color: white;
}

.upgrade-title-level {
    color: rgba(255, 255, 255, 0.3);
}

.upgrade-title-bold {
    font-weight: bold;
}

.material-icons-boost {
    font-size: 2.75rem !important;
}


/* EARN PAGE */
.earn-container {
    overflow: auto;
    height: 100%;
    width: 100%;
    padding-bottom: 128px;
}

.earn {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
}

.earn p {
    font-size: 2.5rem;
}

/* NAVBAR */
.navbar-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.navbar {
    width: 80%;
    position: absolute;
    left: 50%;
    bottom: 16px;
    transform: translateX(-50%);
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: rgba(51, 51, 51, 0.5);
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    border-radius: 32px;
}

.navbar-items {
    height: 70%;
    width: 24%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.3);
    transform: scale(1);
    transition: background-color 0.3s ease, transform 0.2s ease, color 0.3s ease;
    border-radius: 20px;
}

.material-icons {
    color: rgba(255, 255, 255, 0.3);
    font-size: 2rem !important;
}

.navbar-items p {
    font-size: 0.75rem;
}

.navbar-items.active {
    color: white;
    background: rgba(218, 218, 218, 0.1);
    transform: scale(1.25);
}

.navbar-items.active .material-icons {
    color: white;
}

@media screen and (min-width: 764px) {

    .layout,
    .main,
    .boost-container,
    .navbar,
    .prompt-container {
        max-width: 450px;
        margin: 0 auto;
        background-size: 450px;
        background-position: center;
    }
}